<template lang="html">
  <section class="groupCreate">
    <el-form
      :model="formData"
      :rules="rules"
      label-width="100px"
      ref="groupForm"
    >
      <el-form-item label="" prop="name">
        <el-input v-model="formData.name" placeholder="" />
      </el-form-item>

      <el-form-item label="" prop="remark">
        <el-input
          @keyup.native.enter="submitForm('groupForm')"
          v-model="formData.remark"
          type="textarea"
          placeholder=""
        />
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('groupForm')"></el-button>
        <el-button type="info" @click="$router.push({ name: 'usersGroup' })"></el-button>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
export default {
  name: 'groupCreate',
  data () {
    return {
      formData: {
        name: '',
        remark: ''
      },
      rules: {
        name: [
          { required: true, message: '' },
          { max: 20, message: '20' }
        ]
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$httpAPI.postUsersGroupCreate(this.formData).then(() => {
            this.$router.push({ name: 'usersGroup' })
            this.$message.success('')
          }).catch(error => console.log(error))
        }
      })
    }
  }
}
</script>
