  fetchUsersGroupList: options => axios.get('/api-u/group/list', options),
  fetchUsersGroupLItem: options => axios.get('/api-u/group/info', options),
  postUsersGroupCreate: (data, options) => axios.post('/api-u/group/save', data, options),

