<template lang="html">
  <section class="vips-list">
    <!--  -->
    <FilterArea />
    <!--  -->
    <div class="table-list">
      <!--  -->
      <el-table
        :data="tableData"
        size="small"
        highlight-current-row
        border
      >
        <el-table-column type="index" :min-width="30" />

        <el-table-column prop="createTime" label="" :min-width="140">
          <template slot-scope="scope">
            <span>{{scope.row.createTime | time}}</span>
          </template>
        </el-table-column>

        <el-table-column prop="username" label="" :min-width="110">
          <template slot-scope="scope">
            <span>{{scope.row.username}}</span>
            <MoreDetail @on-click="handleViewUserInfo(scope.row.id)" class="pull-right" />
          </template>
        </el-table-column>

        <el-table-column prop="control" label="" :width="50">
          <template slot-scope="scope">
            <i @click="toggleControlStatus(scope.row.id, scope.row.control)" :class="scope.row.control ? 'el-icon-success' : 'el-icon-error'"></i>
          </template>
        </el-table-column>

        <el-table-column prop="nickname" label="" :min-width="90" />

        <el-table-column prop="banlance" label="" :min-width="100">
          <template slot-scope="scope">
            <span>{{scope.row.banlance | RMB}}</span>
          </template>
        </el-table-column>

        <el-table-column prop="win" label="" />

        <el-table-column prop="operations" label="" :min-width="460">
          <template slot-scope="scope">
            <el-button @click="$router.push(`/users/${scope.row.id}/bets`)" type="primary" size="mini"></el-button>
            <el-button @click="$router.push(`/users/${scope.row.id}/rechargeLogs`)" type="primary" size="mini"></el-button>
            <el-button @click="$router.push(`/users/${scope.row.id}/depositStatistics`)" type="primary" size="mini"></el-button>
            <el-button @click="$router.push(`/users/${scope.row.id}/httpLogs`)" type="primary" size="mini"></el-button>
            <el-button @click="showRebateDialog(scope.row.id)" type="primary" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @current-change="handleCurrentChange"
        @size-change="handleSizeChange"
        :current-page="page.current"
        :total="page.total"
        :page-sizes="[10, 20, 40, 100]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
      />
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="320px"
      center
    >
      <ul class="dialog-user-info">
        <li>ID{{currentUser.id}}</li>
        <li>{{currentUser.username}}</li>
        <li>{{currentUser.nickname}}</li>
        <li>{{currentUser.enable ? '' : ''}}</li>
      </ul>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogRebateVisible"
      width="400px"
      center
    >
      <el-form :model="rebateForm" label-width="100px">
        <el-form-item label="%" >
          <el-input v-model="rebateForm.zrss" size="small" />
        </el-form-item>

        <el-form-item label="%" >
          <el-input v-model="rebateForm.dzyy" size="small" />
        </el-form-item>

        <el-form-item label="%" >
          <el-input v-model="rebateForm.hgty" size="small" />
        </el-form-item>

        <el-form-item label="%" >
          <el-input v-model="rebateForm.cpyx" size="small" />
        </el-form-item>

        <el-form-item label="%" >
          <el-input v-model="rebateForm.gcyx" size="small" />
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleRebateFormConfirm" size="small"></el-button>
      </span>
    </el-dialog>
  </section>
</template>

<script>
import FilterArea from '@/components/others/FilterArea'
import MoreDetail from '@/components/base/MoreDetail'

export default {
  components: {
    FilterArea,
    MoreDetail
  },
  data () {
    return {
      tableData: [],
      page: {
        total: 10,
        current: 1,
        size: 10
      },
      dialogVisible: false, // 
      currentUser: {},
      rebateForm: { // 
        zrss: 0,
        dzyy: 0,
        hgty: 0,
        cpyx: 0,
        gcyx: 0
      },
      dialogRebateVisible: false // 
    }
  },
  created () {
    this.fetchUserList({ current: 1, size: this.page.size })
  },
  methods: {
    // 
    handleCurrentChange (currentPage) {
      this.fetchUserList({ current: this.page.current = currentPage, size: this.page.size })
    },
    // 
    handleSizeChange (pageSize) {
      this.fetchUserList({ current: this.page.current = 1, size: this.page.size = pageSize })
    },
    //  
    handleViewUserInfo (userId) {
      this.$axios.get('/api-u/backend/user', { params: { userId } }).then(response => {
        if (response.data.data) {
          this.currentUser = response.data.data
          this.dialogVisible = true
        } else {
          this.$message.warning('')
        }
      })
    },
    // 
    toggleControlStatus (id, status) {
      if (status) status = 0
      else status = 1
      this.$_.forEach(this.tableData, item => {
        if (item.id === id) item.control = status
      })
      // this.$httpAPI.updateUserControlStatus({ userId: id, control: status })
    },
    //  
    showRebateDialog (id) {
      this.$httpAPI.fetchUsersRebate({
        params: { userId: id }
      }).then(response => {
        this.rebateForm = response.data.data
        this.dialogRebateVisible = true
      })
    },
    // 
    handleRebateFormConfirm () {
      this.$httpAPI.updateUsersRebate(this.rebateForm).then(() => {
        this.dialogRebateVisible = false
        this.$message.success('')
      }).catch(() => this.$message.error(''))
    },
    fetchUserList (page) {
      this.$axios.get('/api-u/backend/userList', {
        params: {
          pageNo: page.current,
          pageSize: page.size
        }
      }).then(response => {
        // 
        this.tableData = response.data.data
        // 
        this.page.total = response.data.amount
      }).catch(error => console.log(error))
    }
  }
}
</script>

