<template lang="html">
  <section class="users-group">
    <!--  -->
    <div class="search-container clearfix">
      <el-button
        @click="$router.push({ name: 'usersGroupCreate' })"
        icon="el-icon-circle-plus-outline"
        type="primary"
        size="small"
        class="pull-right"
      ></el-button>
    </div>
    <div class="content-container">
      <el-table
        :data="tableData"
        size="small"
        highlight-current-row
        border
      >
        <el-table-column type="index" />

        <el-table-column prop="groupId" label="ID" :min-width="150" />

        <el-table-column prop="name" label="" />

        <el-table-column prop="remark" label="" />

        <el-table-column prop="operations" label="">
          <template slot-scope="scope">
            <el-button
              @click="$router.push(`/users/group/${scope.row.groupId}/update`)"
              icon="el-icon-edit"
              type="primary"
              size="mini"
            />
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @current-change="handleCurrentChange"
        @size-change="handleSizeChange"
        :current-page="page.current"
        :total="page.total"
        :page-sizes="[10, 20, 40, 100]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
      />
    </div>
  </section>
</template>

<script>
export default {
  name: 'usersGroup',
  data () {
    return {
      tableData: [],
      page: {
        total: 10,
        current: 1,
        size: 10
      }
    }
  },
  created () {
    this.fetchUsersGroupList({ current: 1, size: this.page.size })
  },
  methods: {
    // 
    handleCurrentChange (currentPage) {
      this.fetchUserList({ current: this.page.current = currentPage, size: this.page.size })
    },
    // 
    handleSizeChange (pageSize) {
      this.fetchUserList({ current: this.page.current = 1, size: this.page.size = pageSize })
    },
    fetchUsersGroupList (page) {
      this.$httpAPI.fetchUsersGroupList({
        params: { pageNo: page.current, pageSize: page.size }
      }).then(response => {
        this.tableData = response.data.data
        this.page.total = response.data.amount
      }).catch(error => console.log(error))
    }
  }
}
</script>

<style lang="scss" scoped>
.content-container {
  margin: 20px 0;
}
</style>
